body {
    font-family: 'Open Sans', serif;
    padding-top: 54px;
    color: #767e86;
}

@media (min-width: 992px) {
    body {
        padding-top: 0;
        padding-left: 17rem;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Saira Extra Condensed', serif;
    font-weight: 700;
    text-transform: uppercase;
    color: #343a40;
}

h1 {
    font-size: 6rem;
    line-height: 5.5rem;
}

h2 {
    font-size: 3.5rem;
}

.subheading {
    text-transform: uppercase;
    font-weight: 500;
    font-family: 'Saira Extra Condensed', serif;
    font-size: 1.35rem;
}

.list-social-icons a {
    color: #879099;
}

.list-social-icons .github a:hover {
    color: #24292e;
}

.list-social-icons .linkedin a:hover {
    color: #0077b5;
}

.list-social-icons .stack-overflow a:hover {
    color: #f48024;
}

.list-social-icons .xing a:hover {
    color: #007575;
}

.list-social-icons .telegram a:hover {
    color: #32afed;
}

.list-social-icons .signalapp a:hover {
    color: #3872e8;
}

.list-social-icons .icomoon-findpenguins a:hover {
    color: #3e88cb;
}

.list-social-icons a .fa-lg {
    font-size: 1.75rem;
    margin-top: -1.875rem;
}

.list-icons {
    font-size: 3rem;
}

@media (min-width: 992px) {
    .list-icons {
        margin-left: -19px;
    }
}

.list-icons .list-inline-item i:hover {
    color: #bf2e29;
}

#sideNav .navbar-nav .nav-item .nav-link {
    font-weight: 600;
    text-transform: uppercase;
    transition: transform 0.5s;
}

#sideNav .navbar-nav .nav-item .nav-link:not(.active):hover {
    transform: scale(1.5);
}

@media (min-width: 992px) {
    #sideNav {
        text-align: center;
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        width: 17rem;
        height: 100vh;
    }

    #sideNav .navbar-brand {
        display: flex;
        margin: auto auto 0;
        padding: 0.5rem;
    }

    #sideNav .navbar-brand .img-profile {
        max-width: 14rem;
        max-height: 14rem;
        border: 0.5rem solid rgba(255, 255, 255, 0.2);
    }

    #sideNav .navbar-collapse {
        display: flex;
        align-items: flex-start;
        flex-grow: 0;
        width: 100%;
        margin-bottom: auto;
    }

    #sideNav .navbar-collapse .navbar-nav {
        flex-direction: column;
        width: 100%;
    }

    #sideNav .navbar-collapse .navbar-nav .nav-item {
        display: block;
    }

    #sideNav .navbar-collapse .navbar-nav .nav-item .nav-link {
        display: block;
    }
}

section.resume-section {
    border-bottom: 1px solid #dee2e6;
    padding-top: 5rem !important;
    padding-bottom: 5rem !important;
}

section.resume-section .resume-item .resume-date {
    min-width: unset;
}

@media (min-width: 768px) {
    section.resume-section {
        min-height: 100vh;
    }

    section.resume-section .resume-item .resume-date {
        min-width: 18rem;
    }
}

@media (min-width: 992px) {
    section.resume-section {
        padding-top: 3rem !important;
        padding-bottom: 3rem !important;
    }
}

@media (max-width: 992px) {
    section.resume-section-sm {
        text-align: center;
    }

    section.resume-section-sm .img-profile {
        max-width: 75% !important;
    }
}

@media (max-width: 576px) {
    section.resume-section-sm {
        text-align: center;
        padding-top: 2rem !important;
    }

    h1 {
        font-size: 4rem !important;
    }

    .my-auto .subheading {
        margin-bottom: 1rem !important;
    }
}

.bg-primary {
    background-color: #24292e !important;
}

.text-primary {
    color: #505458 !important;
}

a {
    color: #bf2e29;
}

a:hover, a:focus, a:active {
    color: #24292e;
}

img {
    max-width: 80%;
}

@media (min-width: 992px) {
    .resume-content.img {
        max-width: 300px;
    }
}

.resume-content.img {
    min-width: 300px;
    border-radius: 8px;
    overflow: hidden;
}

.resume-content.img img {
    max-width: 100%;
    min-width: 100%;
    border-radius: 8px;
    transition: all .5s;
}

.resume-content.img:hover img,
.resume-content.img:focus img {
    transform: scale(1.2);
}

/* Back to top button */
#back-to-top {
    display: none;
    position: fixed;
    padding: 1rem;
    bottom: 2.5em;
    right: 2.5em;
    line-height: 1rem;
    color: #585858;
    border-radius: 100px;
    cursor: pointer;
    box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.3);
}

#back-to-top:hover {
    color: #bf2e29;
    box-shadow: 0 0 3px 1px #bf2e29;
}
